import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import DropDownSelect from 'components/dropdown-select';
import DateRange from 'components/date-range';
import Date from 'components/date';
import TotalRange from 'components/total-range';
import InputField from 'components/input-field';
import ToggleSwitch from 'components/toggle-switch';

import Button from 'components/button';
import { useI18n } from 'services/i18n';

import { styledSystem } from 'services/theme/helpers';

import { ableParamsToSearchDocs } from 'store/contants';
import Box from 'primitive-components/box';

export const fieldTypes = {
  DROPDOWN: 'dropdown',
  DATE_RANGE: 'dateRange',
  DATE: 'date',
  INPUT: 'input',
  TOTAL_RANGE: 'totalRange',
  TOGGLE_SWITCH: 'toggleSwitch',
};

const Container = styled(Box)`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 1.25rem;
  width: 100%;
  ${styledSystem};
`;
// Wrappers
const WrapperDateRange = ({ value: { dateIn, dateEnd }, onChange }) => (
  <DateRange
    value={{ dateIn, dateEnd }}
    onChange={onChange}
    mb="2rem"
  />
);
WrapperDateRange.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.objectOf(PropTypes.any),
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperDate = ({ value: { date }, onChange }) => (
  <Date
    value={date}
    onChange={onChange}
    mb="2rem"
  />
);
WrapperDate.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.objectOf(PropTypes.any),
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperDropDown = ({ fieldName, value, ...remainderProps }) => (
  <DropDownSelect
    value={value[fieldName]}
    fieldName={fieldName}
    {...remainderProps}
  />
);
WrapperDropDown.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperToggleSwitch = ({
  fieldName, onChange, value, ...remainderProps
}) => {
  const handleOnChange = (val) => onChange({ [fieldName]: val });
  return (
    <ToggleSwitch
      mb="2rem"
      enabled={value[fieldName]}
      onChange={handleOnChange}
      {...remainderProps}
    />
  );
};

WrapperToggleSwitch.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
  onChange: PropTypes.func,
};

// //////////////////////////////////////////////////////////////////////////////////

const WrapperInput = ({
  fieldName, value, ...remainderProps
}) => (
  <InputField
    value={value[fieldName]}
    fieldName={fieldName}
    {...remainderProps}
  />
);

WrapperInput.propTypes = {
  fieldName: PropTypes.string,
  value: PropTypes.objectOf(PropTypes.any),
};

// //////////////////////////////////////////////////////////////////////////////////

const { TOTAL_END, TOTAL_IN } = ableParamsToSearchDocs;

const WrapperTotalRange = ({ value, onChange }) => (
  <TotalRange
    value={{
      [TOTAL_IN]: value[TOTAL_IN],
      [TOTAL_END]: value[TOTAL_END],
    }}
    onChange={onChange}
  />
);
WrapperTotalRange.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.objectOf(PropTypes.any),
};

// //////////////////////////////////////////////////////////////////////////////////

const Components = {
  [fieldTypes.DROPDOWN]: WrapperDropDown,
  [fieldTypes.DATE_RANGE]: WrapperDateRange,
  [fieldTypes.DATE]: WrapperDate,
  [fieldTypes.INPUT]: WrapperInput,
  [fieldTypes.TOTAL_RANGE]: WrapperTotalRange,
  [fieldTypes.TOGGLE_SWITCH]: WrapperToggleSwitch,
};

// Main Component
const Form = ({
  config, value, submitButton, onChange, ...remainderProps
}) => {
  const [currentValue, setCurrentValue] = useState({});
  const t = useI18n();
  const handleChangeFilter = (change) => {
    const newValue = { ...currentValue, ...change };
    setCurrentValue(newValue);
    onChange?.(newValue);
  };

  useEffect(() => {
    setCurrentValue(value);
  }, [value]);

  const { label, onSubmit, ...submitButtonProps } = submitButton || {};

  return (
    <Container {...remainderProps}>
      {config.map((conf, index) => {
        if (!conf) return null;

        const Component = Components[conf.fieldType];
        if (!Component) return null;

        return <Component key={index} onChange={handleChangeFilter} value={currentValue} {...conf} />;
      })}

      {!!submitButton && (
        <Button
          design="info"
          p="10px 20px"
          m="0 15px 10px 15px"
          height="fit-content"
          onClick={() => onSubmit?.(currentValue)}
          {...submitButtonProps}
        >
            {t(label)}
        </Button>

      )}

    </Container>
  );
};

Form.propTypes = {
  config: PropTypes.arrayOf(
    PropTypes.oneOfType([
      PropTypes.shape({
        fieldType: PropTypes.oneOf(Object.values(fieldTypes)),
        // custom props
      }),
      PropTypes.bool,
    ]),
  ),
  submitButton: PropTypes.shape({
    label: PropTypes.string.isRequired,
    onSubmit: PropTypes.func.isRequired,
    // custom props
  }),
  value: PropTypes.objectOf(PropTypes.any),
  onChange: PropTypes.func,
};

export default Form;
