import React from 'react';
import PropTypes from 'prop-types';
import { useHasRoles } from 'services/hasRoles';
import { useModal } from 'services/modal';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import NewUpdateUser from 'scenes/modals/new-update-area';

import ConfirmModal from 'scenes/modals/confirm';
import { useI18n } from 'services/i18n';
import ActionsTemplate, { IconTypes } from '../ActionsTemplate';

const Actions = ({ data = {} }) => {
  const t = useI18n();
  const { renderComponent } = useModal();
  const handleShowModalUpdateUser = () => renderComponent(
    () => <NewUpdateUser user={data} />,
    { hideFooter: true },
  );

  const handleShowModalDeleteUser = () => renderComponent(
    () => (
      <ConfirmModal
        title="remove_user_confirm_title"
        message="remove_user_confirm"
        onConfirm={() => {
          // use data or dispath here
        }}
      />
    ),
    { hideFooter: true },
  );

  const { user: { full, remove, update } } = useHasRoles();

  const iconsData = [
    (update || full) && { type: IconTypes.EDIT, onClick: handleShowModalUpdateUser }, // TODO
    // { type: IconTypes.REFRESH }, // TODO
    (remove || full) && { type: IconTypes.DELETE, onClick: handleShowModalDeleteUser }, // TODO
  ];

  return (
    <ActionsTemplate icons={iconsData} />
  );
};

export const heads = [
  'actions',
  'number',
  'name',
  'email_title',
  'profile',
  'area',
  'state',
];

const List = ({ data }) => (
  <Box display="flex" flexDirection="column">
    {data?.map((d) => (<Text mt="5px">{d}</Text>))}
  </Box>
);
List.propTypes = {
  data: PropTypes.arrayOf(PropTypes.string),
};

export const transmute = (data) => {
  const {
    number, name, userEmail, userProfile, active,
  } = data;

  const { profileName, profileAreas } = userProfile;

  return [
    <Actions data={data} />,
    number,
    name,
    userEmail,
    profileName,
    <List data={profileAreas.map(({ areaName }) => areaName)} />,
    active ? 'Activo' : 'Inactivo',
  ];
};
