import { combineReducers } from 'redux';
import { createSelector } from 'reselect';
import { call } from 'redux-saga/effects';
import RS from 'helpers/RS';
import { checkDefaultParams } from '../commonSagas';
import { getPagination } from '../helpers';

const NAME = 'documents';

function* FetchDocuments(api, { payload: params }) {
  const updatedParams = yield call(checkDefaultParams, params);
  return yield call(api.getDocs, updatedParams);
}

const Docs1RS = RS({
  nestedPath: [NAME],
  storeName: 'docs1RS',
  tryGenerator: FetchDocuments,
});

const Docs2RS = RS({
  nestedPath: [NAME],
  storeName: 'docs2RS',
  tryGenerator: FetchDocuments,
});

/// ////////////////////////////////////////////////////////////////////////////////

export const sagas = [
  Docs1RS.sagas,
  Docs2RS.sagas,
];

export const reducer = {
  [NAME]: combineReducers({
    ...Docs1RS.reducer,
    ...Docs2RS.reducer,
  }),
};

/// ////////////////////////////////////////////////////////////////////////////////

export const actions = {
  FetchDocs1: Docs1RS.actions.Fetch,
  FetchDocs2: Docs2RS.actions.Fetch,
};

// Selectors
const getDocs1Data = Docs1RS.selectors.getData;
const getDocs1IsBusy = Docs1RS.selectors.getIsBusy;

const getDocs1 = createSelector(
  getDocs1Data,
  (data) => data?.list || [],
);

const getDocs1Pagination = createSelector(
  getDocs1Data,
  (data) => getPagination(data),
);

/// //////////////////////////////////////////////////////////////////////

const getDocs2Data = Docs2RS.selectors.getData;
const getDocs2IsBusy = Docs2RS.selectors.getIsBusy;

const getDocs2 = createSelector(
  getDocs2Data,
  (data) => data?.list || [],
);

const getDocs2Pagination = createSelector(
  getDocs1Data,
  (data) => getPagination(data),
);

export const selectors = {
  getDocs1,
  getDocs1Pagination,
  getDocs1IsBusy,

  getDocs2,
  getDocs2Pagination,
  getDocs2IsBusy,
};
