import React, { useState, useEffect } from 'react';
import DataBox from 'components/data-box';
import Box from 'primitive-components/box';
import { useI18n } from 'services/i18n';
import styled from 'styled-components';
import { useModal } from 'services/modal';
import useSelectors from 'helpers/useSelectors';
import { selectors as userSelectors } from 'store/user';

import Button from 'components/button';
import FormInputGroup from 'components/form-input-group';
import Separator from 'primitive-components/separator';
import ChangePassword from 'scenes/modals/change-password';

const Header = styled.h4`
    color: ${({ theme }) => theme.colors.primaryText};
    font-weight: 600;
    margin-bottom: 0;
    font-size: 1.2rem;
    margin-top: 0;
`;

const EditableInfo = (props) => {
  const t = useI18n();
  const [state, setState] = useState({});
  const { renderComponent } = useModal();

  const [
    name,
    email,
    { value: rfcs },
  ] = useSelectors(
    userSelectors.getUserName,
    userSelectors.getUserEmail,
    userSelectors.getMyRFCsFields,
  );

  useEffect(() => {
    setState({ name, email, rfc: rfcs?.join(',') });
  }, [name, email, rfcs]);

  const handleInputChange = ({ target: { name: nameT, value } }) => setState({ ...state, [nameT]: value });

  const handleShowChangePassword = () => renderComponent(ChangePassword, { hideFooter: true });

  return (
    <DataBox separator title="account_config" {...props}>
      <Box padding="1.4rem">
        <Header>{t('personal_data')}</Header>

        <Box marginTop="15px">
          <FormInputGroup
            label={t('name')}
            name="name"
            value={state.name}
            onChange={handleInputChange}
            placeholder={t('name')}
          />
          <FormInputGroup
            label={t('email')}
            name="email"
            value={state.email}
            onChange={handleInputChange}
            placeholder={t('email')}
          />
          <FormInputGroup
            label={t('phone')}
            name="phone"
            onChange={handleInputChange}
            placeholder={t('phone')}
          />
          <Box marginTop=".5rem">
            <Button
              design="primary"
              ml="auto"
              onClick={handleShowChangePassword}
            >
              {t('change_password')}
            </Button>
          </Box>
        </Box>
      </Box>

      <Box padding="1.4rem">
        <Header>{t('tax_data')}</Header>

        <Box marginTop="15px">
          <FormInputGroup
            name="rfc"
            label={t('rfc')}
            placeholder={t('rfc')}
            onChange={handleInputChange}
            value={state.rfc}
          />
          <FormInputGroup
            name="socialReason"
            label={t('social_reason')}
            onChange={handleInputChange}
            placeholder={t('social_reason')}
          />
          <FormInputGroup
            name="domicile"
            label={t('domicile')}
            onChange={handleInputChange}
            placeholder={t('domicile')}
          />
          <FormInputGroup
            name="postalCode"
            label={t('postal_code')}
            onChange={handleInputChange}
            placeholder={t('postal_code')}
          />
        </Box>
      </Box>

      <Separator m="20px 0" border="1px dashed #eee" />

      <Box textAlign="right">
        <Button marginLeft="auto" rounded design="default">{t('save_changes')}</Button>
      </Box>
    </DataBox>
  );
};

export default EditableInfo;
