import { call } from 'redux-saga/effects';
import RS from 'helpers/RS';
// import { ableParamsToFetch, docsTypes } from 'store/contants';

function* AddPurchaseOrder(api, { payload: data }) {
  const params = {
    // [ableParamsToFetch.TYPE_DOC]: docsTypes.PURCHASE,
  };

  const response = yield call(api.insertDocument, { params, data });
  return response;
}

const AddPurchaseOrderRS = RS({
  nestedPath: [],
  storeName: 'add-purchase-order',
  tryGenerator: AddPurchaseOrder,
});

export const sagas = [
  AddPurchaseOrderRS.sagas,
];

export const { reducer } = AddPurchaseOrderRS;

export const actions = {
  AddPurchaseOrder: AddPurchaseOrderRS.actions.Fetch,

  SetNoStarted: AddPurchaseOrderRS.actions.NoStarted,
};

// Selectors
export const selectors = {
  getIsBusy: AddPurchaseOrderRS.selectors.getIsBusy,
  getIsSuccess: AddPurchaseOrderRS.selectors.getIsSuccess,
  getIsFailed: AddPurchaseOrderRS.selectors.getIsFailed,
};
