import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import { cfdiPropType } from 'helpers/propTypes';
import DataBox from 'components/data-box';
import { actions, selectors } from 'store/relatedDocs';
import TableBox, { tableTypes, tableSizes } from 'components/table-box';

const RelatedDocs = ({ doc }) => {
  const [page, setPage] = useState(1);
  const dispatch = useDispatch();

  const [
    isBusy,
    relatedDocs,
    pagination,
  ] = useSelectors(
    selectors.getRelatedDocsIsBusy,
    selectors.getRelatedDocs,
    selectors.getRelatedDocsPagination,
  );

  const { uuid, type, noOrdenC } = doc;
  const handleFetchRelatedDocs = () => dispatch(actions.FetchRelatedDocs({
    page, uuid, noOrdenC, typedoc: type,
  }));

  useEffect(() => {
    if (doc) handleFetchRelatedDocs();
  }, [doc, page]);

  return (
    <DataBox title="related_docs" separator width="50rem">
      <TableBox
        tableType={tableTypes.RELATED_DOCS}
        tableSize={tableSizes.XS}
        elements={relatedDocs}
        noFoundMessage="no_related_docs_found"
        isBusy={isBusy}
        onChangePage={setPage}
        pagination={pagination}
      />
    </DataBox>
  );
};

RelatedDocs.propTypes = {
  doc: cfdiPropType,
};
export default RelatedDocs;
