import React from 'react';
import { Switch, Route, Redirect } from 'react-router';
import Layout from 'components/layout';

import UsersRegistration, { PATH as pathUserRegistration } from 'scenes/common/users-registration';

import Profile, { PATH as pathProfile } from 'scenes/common/profile';
import SideBar from './SideBar';
import Home, { PATH as pathHome } from './home';
import CFDIs, { PATH as pathCFDIs } from './cfdis';
import PaymentComplements, { PATH as pathPaymentComplements } from './payment-complements';
import PurchaseOrders, { PATH as pathPurchaseOrders } from './purchase-orders';
import Reports, { PATH as pathReports } from './reports';

import { PATH as parentPath } from '../constants';

const Content = () => (
  <Switch>
    <Route exact path={parentPath} render={() => <Redirect to={pathHome} />} />
    <Route path={pathHome} component={Home} />
    <Route path={pathCFDIs} component={CFDIs} />
    <Route path={pathPaymentComplements} component={PaymentComplements} />
    <Route path={pathPurchaseOrders} component={PurchaseOrders} />
    <Route path={pathReports} component={Reports} />

    <Route path={`${parentPath}${pathUserRegistration}`} component={UsersRegistration} />
    <Route path={`${parentPath}${pathProfile}`} component={Profile} />
    <Route path="*" render={() => <Redirect to={pathHome} />} />
  </Switch>
);

export default () => <Layout sideBar={SideBar} content={Content} />;
