import React, { useEffect, useState } from 'react';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import { useModal } from 'services/modal';
import { useHasRoles } from 'services/hasRoles';
import { isUndefined, some } from 'lodash';
import filter, { ExceptionNumberToString } from 'helpers/filter';
import useSearch from 'services/search/useSearch';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import { ableAreaFields, ableRfcsFields } from 'store/contants';
import { actions as entityActions, selectors as entitySelectors } from 'store/entity';
import TableBox, { tableTypes } from 'components/table-box';
import {
  SvgPlusSolid,
  SvgUploadSolid,
  SvgDownloadSolid,
  SvgSyncSolid,
} from 'react-line-awesome-svg';
import NewUpdateArea from 'scenes/modals/new-update-area';
import { roles } from '../constants';

const role = roles[0];
const valuesAbleAreaFields = Object.values(ableAreaFields);

const exceptionsToFilter = {
  [ableAreaFields.AREA_RFCS]: (value, matcher) => some(value, (subValue) => matcher.includes(subValue[ableRfcsFields.CODE])),
  [ableAreaFields.AREA_NAME]: ExceptionNumberToString,
};

const AreaManagement = () => {
  const [filteredAreas, setFilteredAreas] = useState([]);

  const dispatch = useDispatch();
  const { search: { [role]: search = '' }, params: { [role]: params = {} }, updateParams: updateParamsT } = useSearch();
  const updateParams = (p) => updateParamsT(p, role);

  const { renderComponent } = useModal();
  const { area: { create, full } } = useHasRoles();

  const [
    areas,
    areaIsBusy,
    entityRfcs,
  ] = useSelectors(
    entitySelectors.getEntityAreaData,
    entitySelectors.getEntityAreaRetrieveIsBusy,
    entitySelectors.getEntityRfcs,
  );

  const handleShowModalNewArea = () => renderComponent(NewUpdateArea, { hideFooter: true });
  const handleFetchAreas = () => dispatch(entityActions.EntityAreaRetrieve());

  const filterArea = () => (
    setFilteredAreas(filter({
      items: areas,
      itemKeys: valuesAbleAreaFields,
      matcherItem: params,
      exceptions: exceptionsToFilter,
    }))
  );

  useEffect(() => {
    handleFetchAreas();
  }, []);

  useEffect(() => {
    if (isUndefined(params[ableAreaFields.ACTIVE])) updateParams({ [ableAreaFields.ACTIVE]: true });
  }, [params]);

  useEffect(() => {
    filterArea();
  }, [areas, params]);

  const headBtnsConfig = [
    (create || full) && {
      id: 'new', design: 'primary', svgIcon: SvgPlusSolid, onClick: handleShowModalNewArea,
    },
    { id: 'import', design: 'default', svgIcon: SvgUploadSolid },
    { id: 'export', design: 'info', svgIcon: SvgDownloadSolid },
    {
      id: 'update', design: 'shadow', svgIcon: SvgSyncSolid, rounded: true, onClick: handleFetchAreas,
    },
  ];

  const formConfig = [
    {
      label: 'area',
      fieldType: fieldTypes.INPUT,
      fieldName: ableAreaFields.AREA_NAME,
    },
    {
      label: 'rfcs',
      enableSearch: true,
      items: entityRfcs,
      optionTextField: 'code',
      optionValueField: 'code',
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableAreaFields.AREA_RFCS,
    },
    {
      label: 'state',
      fieldType: fieldTypes.TOGGLE_SWITCH,
      fieldName: ableAreaFields.ACTIVE,
    },
  ];

  return (
    <DataBox title="area_management" separator headBtnsConfig={headBtnsConfig}>
      <Form
        config={formConfig}
        value={params}
        onChange={updateParams}
      />

      <TableBox
        tableType={tableTypes.AREA_MANAGEMENT}
        elements={filteredAreas}
        noFoundMessage="no_areas_found"
        isBusy={areaIsBusy}
      />
    </DataBox>
  );
};

export default AreaManagement;
