import React from 'react';
import PropTypes from 'prop-types';
import { useModal } from 'services/modal';
import { useHasRoles } from 'services/hasRoles';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import NewUpdateArea from 'scenes/modals/new-update-area';

import ActionsTemplate, { IconTypes } from '../ActionsTemplate';

const Actions = ({ data = {} }) => {
  const { renderComponent } = useModal();
  const handleShowModalUpdateArea = () => renderComponent(
    () => <NewUpdateArea area={data} />,
    { hideFooter: true },
  );

  const { area: { full, remove, update } } = useHasRoles();

  const iconsData = [
    (update || full) && { type: IconTypes.EDIT, onClick: handleShowModalUpdateArea },
    (remove || full) && { type: IconTypes.DELETE }, // TODO
  ];

  return (
    <ActionsTemplate icons={iconsData} />
  );
};

export const heads = [
  'actions',
  'area',
  'rfcs',
  'company_name',
  'state',
];

const List = ({ data }) => (
  <Box display="flex" flexDirection="column">
    {data?.map((d, index) => (<Text key={index} mt="5px">{d}</Text>))}
  </Box>
);
List.propTypes = {
  data: PropTypes.arrayOf(PropTypes.string),
};

export const transmute = (data) => {
  const {
    areaName, active, areaRfcs, mutable,
  } = data;

  return [
    <Actions data={data} />,
    areaName,
    <List data={areaRfcs.map(({ code }) => code)} />,
    <List data={areaRfcs.map(({ company }) => company)} />,
    active ? 'Activa' : 'Inactiva',
  ];
};
