import React from 'react';
import { useHasRoles } from 'services/hasRoles';
import { useModal } from 'services/modal';
import NewUpdateProfile from 'scenes/modals/new-update-profile';

import ActionsTemplate, { IconTypes } from '../ActionsTemplate';

const Actions = ({ data = {} }) => {
  const { renderComponent } = useModal();
  const handleOpenProfileManagement = () => renderComponent(
    () => <NewUpdateProfile data={data} />,
    { hideFooter: true },
  );

  const { profile: { full, remove, update } } = useHasRoles();

  const iconsData = [
    (update || full) && { type: IconTypes.EDIT, onClick: handleOpenProfileManagement }, // TODO
    (remove || full) && { type: IconTypes.DELETE }, // TODO
  ];

  return (
    <ActionsTemplate icons={iconsData} />
  );
};

export const heads = [
  'actions',
  'profile_name',
  'amount_of_users',
  'amount_of_rules',
];

export const transmute = (data) => {
  const {
    profileName,
    amountOfUsers,
    amountOfRules,
  } = data;

  return [
    <Actions data={data} />,
    profileName,
    amountOfUsers,
    amountOfRules,
  ];
};
