import { select, call } from 'redux-saga/effects';
import { selectors as userSelectors } from 'store/user';
import {
  pageSize,
  getDefaultDates,
  ableParamsToSearchDocs,
} from './contants';

export function* getDefaultRFCParams() {
  const { key, value } = yield select(userSelectors.getMyRFCsFields);
  return { [key]: value };
}

export function* checkDefaultParams(params) {
  const rfcParams = yield call(getDefaultRFCParams);
  return {
    ...rfcParams,
    [ableParamsToSearchDocs.PAGE]: 1,
    [ableParamsToSearchDocs.PAGE_SIZE]: pageSize,
    ...getDefaultDates(),
    ...params,
  };
}

export function* checkDefaultReportsParams(params) {
  const rfcParams = yield call(getDefaultRFCParams);

  return {
    ...rfcParams,
    ...getDefaultDates(),
    ...params,
  };
}
