import { combineReducers } from 'redux';
import { createSelector } from 'reselect';
import { call, put } from 'redux-saga/effects';
import RS from 'helpers/RS';

const NAME = 'relateDocs';

const ITEMS_NAME = 'itemsToRelate';

export const SET_SELECTED_ITEMS_TO_RELATE = `${ITEMS_NAME}.SET_SELECTED_ITEMS_TO_RELATE`;
export const SetSelectedItemsToRelate = (data) => ({
  type: SET_SELECTED_ITEMS_TO_RELATE,
  payload: data,
});

export const SET_SELECTED_ALL_TO_RELATE = `${ITEMS_NAME}.SET_SELECTED_ALL_TO_RELATE`;
export const SetSelectedAllToRelate = (data) => ({
  type: SET_SELECTED_ALL_TO_RELATE,
  payload: data,
});

const defaultState = {
  selectedItems: [],
  selectedAll: false,
};

const ItemReducer = (state = defaultState, action = {}) => {
  switch (action.type) {
    case SET_SELECTED_ITEMS_TO_RELATE:
      return {
        ...state,
        selectedItems: action.payload,
      };
    case SET_SELECTED_ALL_TO_RELATE:
      return {
        ...state,
        selectedAll: action.payload,
      };
    default:
      return state;
  }
};

const getItemsToRelateData = (state) => state[NAME][ITEMS_NAME];

const getSelectedItems = createSelector(
  getItemsToRelateData,
  (data) => data?.selectedItems || [],
);

const getSelectedAll = createSelector(
  getItemsToRelateData,
  (data) => !!data.selectedAll,
);

/// ////////////////////////////////////////////////////////////////////////////////

function* relateDocs(api, { payload: params }) {
  const response = yield call(api.relateDocs, params);
  yield put(SetSelectedItemsToRelate([]));
  yield put(SetSelectedAllToRelate(false));
  return response;
}

const relateDocsRS = RS({
  nestedPath: [NAME],
  storeName: 'relateDocs',
  tryGenerator: relateDocs,
});

// /////////////////////////////////////////////////////////////////////////////////

export const sagas = [
  relateDocsRS.sagas,
];

export const reducer = {
  [NAME]: combineReducers({
    ...relateDocsRS.reducer,
    [ITEMS_NAME]: ItemReducer,
  }),
};

export const actions = {
  RelateDocs: relateDocsRS.actions.Fetch,
  SetRelateDocsNotStarted: relateDocsRS.actions.NoStarted,

  SetSelectedItemsToRelate,
  SetSelectedAllToRelate,
};

// Selectors
export const selectors = {
  getRelateDocsIsBusy: relateDocsRS.selectors.getIsBusy,
  getRelateDocsIsFailed: relateDocsRS.selectors.getIsFailed,
  getRelateDocsIsSuccess: relateDocsRS.selectors.getIsSuccess,

  getSelectedItems,
  getSelectedAll,
};
