import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useI18n } from 'services/i18n';
import useSelectors from 'helpers/useSelectors';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import { useModal } from 'services/modal';
import { ableAreaFields, ableAreaRfcsFields } from 'store/contants';
import { selectors as authSelectors } from 'store/auth';
import { actions as entityActions, selectors as entitySelectors } from 'store/entity';
import Button from 'components/button';

const defaultArea = {
  [ableAreaFields.AREA_NAME]: '',
  [ableAreaFields.AREA_RFCS]: [],
};

const areSameAreas = (area1, area2) => area1[ableAreaFields.AREA_NAME] === area2[ableAreaFields.AREA_NAME];

const NewUpdateArea = ({ area }) => {
  const [currentArea, setCurrentArea] = useState(defaultArea);
  const [indexCurrentArea, setIndexCurrentArea] = useState(-1);

  const dispatch = useDispatch();
  const t = useI18n();

  const { onClose: onModalClose, ModalFooter } = useModal();
  const titleTag = area ? 'update_area' : 'new_area';

  const [
    entityRfcs,
    entityAreas,

    isBusyAddArea,
    isSuccessAddArea,

    isBusyUpdateAreas,
    isSuccessUpdateAreas,
  ] = useSelectors(
    entitySelectors.getEntityRfcs,
    entitySelectors.getEntityAreaData,

    entitySelectors.getAddAreaIsBusy,
    entitySelectors.getAddAreaIsSuccess,

    entitySelectors.getUpdateAreasIsBusy,
    entitySelectors.getUpdateAreasIsSuccess,
  );

  const handleAddArea = () => dispatch(entityActions.AddArea(currentArea));
  const handleUpdateAreas = () => {
    const newAreas = [...entityAreas];
    newAreas[indexCurrentArea] = currentArea;
    dispatch(entityActions.UpdateAreas(newAreas));
  };
  const handleOnSubmit = area ? handleUpdateAreas : handleAddArea;

  useEffect(() => {
    if (isSuccessAddArea || isSuccessUpdateAreas) {
      dispatch(entityActions.EntityAreaRetrieve());
      onModalClose();
    }
  }, [isSuccessAddArea, isSuccessUpdateAreas]);

  useEffect(() => {
    if (area) {
      const index = entityAreas?.findIndex((entityArea) => areSameAreas(entityArea, area));
      setIndexCurrentArea(index);
      setCurrentArea(area);
    }
  }, [area]);

  const formConfig = [
    {
      label: 'area',
      fieldType: fieldTypes.INPUT,
      fieldName: ableAreaFields.AREA_NAME,
    },
    {
      label: 'rfcs',
      items: entityRfcs,
      enableSearch: true,
      optionTextField: 'code',
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableAreaFields.AREA_RFCS,
    },
    !!area && {
      label: 'state',
      fieldType: fieldTypes.TOGGLE_SWITCH,
      fieldName: ableAreaFields.ACTIVE,
    },
  ];

  return (
    <>
      <DataBox title={titleTag} separator width="35rem">
        <Form
          config={formConfig}
          value={currentArea}
          onChange={setCurrentArea}
        />
      </DataBox>
      <ModalFooter showSpinner={isBusyAddArea || isBusyUpdateAreas}>
        <Button design="primary" rounded m="10px" p="12px 30px" onClick={handleOnSubmit}>{t('upload')}</Button>
      </ModalFooter>
    </>
  );
};

export default NewUpdateArea;
