import React from 'react';
import Button from 'components/button';
import ActionsTemplate, { IconTypes } from '../ActionsTemplate';

const Actions = ({ data = {} }) => {
  const iconsData = [
    { type: IconTypes.EDIT }, // TODO
    { type: IconTypes.REFRESH }, // TODO
    { type: IconTypes.DELETE }, // TODO
  ];

  return (
    <ActionsTemplate icons={iconsData} />
  );
};

export const heads = [
  'actions',
  'number',
  'rfc',
  'user',
  'social_reason',
  'email',
  'active',
  'blocked',
];

export const transmute = (data) => {
  const {
    number, rfc, name, email, blocked, status,
  } = data;

  return [
    <Actions data={data} />,
    number,
    rfc,
    name,
    'ni idea',
    email,
    status,
    blocked
      ? <Button rounded design="primary">Si</Button>
      : <Button rounded design="error">No</Button>,
  ];
};
