import React, { useEffect } from 'react';
import Page from 'components/page';
import useSearch from 'services/search/useSearch';
import { actions as entityActions } from 'store/entity';
import { useDispatch } from 'react-redux';
import AreaManagement from './AreaManagement';
import ProfileManagement from './ProfileManagement';
import UserManagement from './UserManagement';

import { roles } from '../constants';

const UsersRegistration = () => {
  const dispatch = useDispatch();
  const { createRoles } = useSearch();

  useEffect(() => {
    createRoles(roles);
    dispatch(entityActions.EntityRetrieve());
  }, []);

  return (
    <Page title="users_registration">
      <AreaManagement />
      <ProfileManagement />
      <UserManagement />
    </Page>
  );
};

export default UsersRegistration;
