import React, { useEffect, useState } from 'react';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import useSearch from 'services/search/useSearch';
import { useDispatch } from 'react-redux';
import filter, { ExceptionNumberToString } from 'helpers/filter';
import { isUndefined, some } from 'lodash';

import TableBox, { tableTypes } from 'components/table-box';
import { actions as entityActions, selectors as entitySelectors } from 'store/entity';
import { useHasRoles } from 'services/hasRoles';

import {
  SvgPlusSolid,
  SvgUploadSolid,
  SvgDownloadSolid,
  SvgSyncSolid,
} from 'react-line-awesome-svg';
import useSelectors from 'helpers/useSelectors';
import { useModal } from 'services/modal';
import { ableUserFields, ableProfileFields } from 'store/contants';
import ConfirmModal from 'scenes/modals/confirm';
import NewUpdateUser from 'scenes/modals/new-update-user';
import { roles } from '../constants';

const role = roles[2];

const valuesAbleUserFields = Object.values(ableUserFields);

const exceptionsToFilter = {
  [ableUserFields.USER_PROFILE]: (value, matcher) => some(value, (subValue) => matcher.includes(subValue[ableProfileFields.PROFILE_NAME])),
};

const UserManagement = () => {
  const [filteredUsers, setFilteredUsers] = useState([]);

  const dispatch = useDispatch();
  const { renderComponent } = useModal();

  const { search: { [role]: search = '' }, params: { [role]: params = {} }, updateParams: updateParamsT } = useSearch();
  const updateParams = (p) => updateParamsT(p, role);
  const { user: { full, create } } = useHasRoles();

  const [
    users,
    userIsBusy,
    profiles,
  ] = useSelectors(
    entitySelectors.getEntityUserData,
    entitySelectors.getEntityUserRetrieveIsBusy,
    entitySelectors.getEntityProfileData,
  );

  const handleFetchUsers = () => dispatch(entityActions.EntityUserRetrieve());
  const handleShowModalNewUser = () => renderComponent(NewUpdateUser, { hideFooter: true });

  const filterUsers = () => (
    setFilteredUsers(filter({
      items: users,
      itemKeys: valuesAbleUserFields,
      matcherItem: params,
      exceptions: exceptionsToFilter,
    }))
  );

  const headBtnsConfig = [
    (create || full) && {
      id: 'new', design: 'primary', svgIcon: SvgPlusSolid, onClick: handleShowModalNewUser,
    },
    { id: 'import', design: 'default', svgIcon: SvgUploadSolid },
    { id: 'export', design: 'info', svgIcon: SvgDownloadSolid },
    {
      id: 'update', design: 'shadow', svgIcon: SvgSyncSolid, rounded: true, onClick: handleFetchUsers,
    },
  ];

  const formConfig = [
    {
      label: 'name',
      fieldType: fieldTypes.INPUT,
      fieldName: ableUserFields.NAME,
    },
    {
      label: 'email',
      fieldType: fieldTypes.INPUT,
      fieldName: ableUserFields.USER_EMAIL,
    },
    {
      label: 'profile',
      enableSearch: true,
      items: profiles,
      optionTextField: ableProfileFields.PROFILE_NAME,
      optionValueField: ableProfileFields.PROFILE_NAME,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableUserFields.USER_PROFILE,
    },
    {
      label: 'state',
      fieldType: fieldTypes.TOGGLE_SWITCH,
      fieldName: ableUserFields.ACTIVE,
    },
  ];

  useEffect(() => {
    dispatch(entityActions.EntityUserRetrieve());
  }, []);

  useEffect(() => {
    if (isUndefined(params[ableUserFields.ACTIVE])) updateParams({ [ableUserFields.ACTIVE]: true });
  }, [params]);

  useEffect(() => {
    setFilteredUsers(users);
  }, [users]);

  useEffect(() => {
    filterUsers();
  }, [users, params]);

  return (
    <DataBox title="user_management" separator headBtnsConfig={headBtnsConfig}>
      <Form
        config={formConfig}
        value={params}
        onChange={updateParams}
      />

      <TableBox
        tableType={tableTypes.USER_MANAGEMENT}
        elements={filteredUsers}
        noFoundMessage="no_users_found"
        isBusy={userIsBusy}
      />
    </DataBox>
  );
};

export default UserManagement;
