import { createSelector } from 'reselect';
import { call } from 'redux-saga/effects';
import RS from 'helpers/RS';
import { getPagination } from '../helpers';

function* relatedDocs(api, { payload: params }) {
  return yield call(api.fetchRelatedDocs, params);
}

const relatedDocsRS = RS({
  nestedPath: [],
  storeName: 'relatedDocs',
  tryGenerator: relatedDocs,
});

export const sagas = [
  relatedDocsRS.sagas,
];

export const { reducer } = relatedDocsRS;

export const actions = {
  FetchRelatedDocs: relatedDocsRS.actions.Fetch,
};

// Selectors
const getRelatedDocsData = relatedDocsRS.selectors.getData;
const getRelatedDocs = createSelector(
  getRelatedDocsData,
  (data) => data?.list || [],
);

const getRelatedDocsPagination = createSelector(
  getRelatedDocsData,
  (data) => getPagination(data),
);

export const selectors = {
  getRelatedDocs,
  getRelatedDocsPagination,
  getRelatedDocsIsBusy: relatedDocsRS.selectors.getIsBusy,
};
