import { Auth, Amplify, API as API_AWS } from 'aws-amplify';
import { paramsSerializer, toAPI } from './tranmuter';

import authConfig, { INGESTA, MANAGEMENT, DB_SERVICES } from './authConfig';

Amplify.configure(authConfig);

const API = ({
  method,
  endpoint,
  path,
  params,
  data: body,
}) => {
  // let headers = {};
  // if (endpoint === DB_SERVICES) {
  //   const { attributes: { email } } = await Auth.currentUserInfo();
  //   headers = { email };
  // }
  const pathWithQueries = params ? `${path}?${paramsSerializer(params)}` : path;
  return API_AWS[method](endpoint, pathWithQueries, { body });
};

