import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useI18n } from 'services/i18n';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import { useModal } from 'services/modal';
import { ableUserFields } from 'store/contants';
import Button from 'components/button';
import { actions as entityActions, selectors as entitySelectors } from 'store/entity';
import useSelectors from 'helpers/useSelectors';

const defaultUser = {
  [ableUserFields.USER_NAME]: '',
  [ableUserFields.USER_EMAIL]: '',
  [ableUserFields.USER_PROFILE]: '',
  [ableUserFields.ACTIVE]: false,
};

const areSameUsers = (user1, user2) => user1[ableUserFields.USER_NAME] === user2[ableUserFields.USER_NAME];

const NewUpdateUser = ({ user }) => {
  const dispatch = useDispatch();
  const t = useI18n();

  const [currentUser, setCurrentUser] = useState(defaultUser);
  const [indexCurrentUser, setIndexCurrentUser] = useState(-1);

  const { onClose: onModalClose, ModalFooter } = useModal();
  const titleTag = user ? 'update_user' : 'new_user';

  const [
    entityUsers,
    entityProfiles,

    isBusyAddUser,
    isSuccessAddUser,

    isBusyUpdateUsers,
    isSuccessUpdateUsers,
  ] = useSelectors(
    entitySelectors.getEntityUserData,
    entitySelectors.getEntityProfileData,

    entitySelectors.getAddUserIsBusy,
    entitySelectors.getAddUserIsSuccess,

    entitySelectors.getUpdateUsersIsBusy,
    entitySelectors.getUpdateUsersIsSuccess,
  );

  useEffect(() => {
    if (isSuccessAddUser || isSuccessUpdateUsers) {
      dispatch(entityActions.EntityUserRetrieve());
      onModalClose();
    }
  }, [isSuccessAddUser, isSuccessUpdateUsers]);

  useEffect(() => {
    if (user) {
      const index = entityUsers?.findIndex((entityUser) => areSameUsers(entityUser, user));
      setIndexCurrentUser(index);
      setCurrentUser(user);
    }
  }, [user]);

  const formConfig = [
    {
      label: 'name',
      fieldType: fieldTypes.INPUT,
      fieldName: ableUserFields.USER_NAME,
    },
    {
      label: 'email_title',
      enableSearch: true,
      fieldType: fieldTypes.INPUT,
      fieldName: ableUserFields.USER_EMAIL,
    },
    {
      label: 'profile',
      items: entityProfiles,
      optionTextField: 'profileName',
      enableSearch: true,
      single: true,
      fieldType: fieldTypes.DROPDOWN,
      fieldName: ableUserFields.USER_PROFILE,
    },
    !!user && {
      label: 'state',
      fieldType: fieldTypes.TOGGLE_SWITCH,
      fieldName: ableUserFields.ACTIVE,
    },
  ];

  const handleAddUser = () => {
    const {
      name, userEmail, userProfile: { profileName },
    } = currentUser;
    dispatch(entityActions.AddUser({
      name, userEmail, userProfile: profileName,
    }));
  };

  const handleUpdateUsers = () => {
    const newUsers = [...entityUsers];
    newUsers[indexCurrentUser] = currentUser;
    const {
      id, name, userEmail, active, userProfile: { profileName },
    } = currentUser;
    dispatch(entityActions.UpdateUsers(
      {
        id, name, userEmail, active, userProfile: profileName,
      },
    ));
  };
  const handleOnSubmit = user ? handleUpdateUsers : handleAddUser;

  return (
    <>
      <DataBox title={titleTag} separator width="35rem">
        <Form
          config={formConfig}
          value={currentUser}
          onChange={setCurrentUser}
        />
      </DataBox>
      <ModalFooter showSpinner={isBusyAddUser || isBusyUpdateUsers}>
        <Button design="primary" rounded m="10px" p="12px 30px" onClick={handleOnSubmit}>{t('upload')}</Button>
      </ModalFooter>
    </>
  );
};

export default NewUpdateUser;
