const NAME = 'modal';

// ACTIONS
const SET_COMPONENT_TO_RENDER = `${NAME}.SET_COMPONENT_TO_RENDER`;
const SetComponentToRender = (data) => ({
  type: SET_COMPONENT_TO_RENDER,
  payload: data,
});

const SET_OPTIONS_TO_RENDER = `${NAME}.SET_OPTIONS_TO_RENDER`;
const SetOptionsToRender = (data) => ({
  type: SET_OPTIONS_TO_RENDER,
  payload: data,
});

export const actions = {
  SetComponentToRender,
  SetOptionsToRender,
};

// REDUCER
const defaultState = {
  component: null,
  options: {},
};

const ModalReducer = (state = defaultState, action = {}) => {
  switch (action.type) {
    case SET_COMPONENT_TO_RENDER:
      return {
        ...state,
        component: action.payload,
      };
    case SET_OPTIONS_TO_RENDER:
      return {
        ...state,
        options: action.payload,
      };
    default:
      return state;
  }
};

export const reducer = {
  [NAME]: ModalReducer,
};

// SELECTORS
const getComponent = (state) => state[NAME].component;
const getOptions = (state) => state[NAME].options;

export const selectors = {
  getComponent,
  getOptions,
};
