import React, { useEffect, useState } from 'react';
import DataBox from 'components/data-box';
import Form, { fieldTypes } from 'components/form';
import useSearch from 'services/search/useSearch';
import { useHasRoles } from 'services/hasRoles';
import { useDispatch } from 'react-redux';
import { ableProfileFields } from 'store/contants';
import TableBox, { tableTypes } from 'components/table-box';
import { SvgPlusSolid, SvgSyncSolid } from 'react-line-awesome-svg';
import { actions as entityActions, selectors as entitySelectors } from 'store/entity';
import useSelectors from 'helpers/useSelectors';
import { roles } from '../constants';

const role = roles[1];

const ProfileManagement = () => {
  const [tabelData, setTableData] = useState([]);
  const dispatch = useDispatch();
  const { search: { [role]: search = '' }, params: { [role]: params = {} }, updateParams: updateParamsT } = useSearch();
  const updateParams = (p) => updateParamsT(p, role);

  const { user: { full, update } } = useHasRoles();

  const handleFetchProfiles = () => dispatch(entityActions.EntityProfileRetrieve());

  const [
    entityProfiles,
    profileIsBusy,
    entityUsers,
  ] = useSelectors(
    entitySelectors.getEntityProfileData,
    entitySelectors.getEntityProfileRetrieveIsBusy,
    entitySelectors.getEntityUserData,
  );

  const headBtnsConfig = [
    (update || full) && { id: 'new', design: 'primary', svgIcon: SvgPlusSolid },
    {
      id: 'update', design: 'shadow', svgIcon: SvgSyncSolid, rounded: true, onClick: handleFetchProfiles,
    },
  ];

  const formConfig = [
    {
      label: 'profile_name',
      fieldType: fieldTypes.INPUT,
      fieldName: ableProfileFields.PROFILE_NAME,
    },
  ];

  useEffect(() => {
    dispatch(entityActions.EntityProfileRetrieve());
  }, []);

  useEffect(() => {
    if (entityProfiles && entityUsers) {
      setTableData(entityProfiles?.map((profile) => ({
        [ableProfileFields.PROFILE_NAME]: profile[ableProfileFields.PROFILE_NAME],
        amountOfUsers: entityUsers.filter((user) => user?.userProfile?.profileName === profile?.profileName).length,
        amountOfRules: '<dev>', // TODO
      })));
    }
  }, [entityProfiles, entityUsers]);

  return (
    <DataBox title="profile_management" separator headBtnsConfig={headBtnsConfig}>
      <Form
        config={formConfig}
        value={params}
        onChange={updateParams}
      />

      <TableBox
        tableType={tableTypes.PROFILE_MANAGEMENT}
        elements={tabelData}
        noFoundMessage="no_profiles_found"
        isBusy={profileIsBusy}
      />
    </DataBox>
  );
};

export default ProfileManagement;
