import { combineReducers } from 'redux';
import { createSelector } from 'reselect';
import { call, select } from 'redux-saga/effects';
import { selectors as userSelectors } from 'store/user';
import RS from 'helpers/RS';

const NAME = 'entity';

// Entity
function* retrieveEntityData(api) {
  const entityId = yield select(userSelectors.getEntityId);
  const { value } = yield call(api.getEntityRetrieve, entityId);
  return value;
}

const entityRetrieveRS = RS({
  nestedPath: [NAME],
  storeName: 'all',
  tryGenerator: retrieveEntityData,
});

// Area
function* retrieveAreaData(api) {
  const { areasList } = yield call(retrieveEntityData, api);
  return areasList;
}

const areaRetrieveRS = RS({
  nestedPath: [NAME],
  storeName: 'areas',
  tryGenerator: retrieveAreaData,
});

function* addArea(api, { payload: area }) {
  const id = yield select(userSelectors.getEntityId);
  const response = yield call(api.addEntityAreas, { id, areasList: [area] });
  return response;
}

const addAreaRS = RS({
  nestedPath: [NAME],
  storeName: 'addArea',
  tryGenerator: addArea,
});

function* updateAreas(api, { payload: areas }) {
  const id = yield select(userSelectors.getEntityId);
  const response = yield call(api.updateEntityAreas, { id, areasList: areas });
  return response;
}

const updateAreasRS = RS({
  nestedPath: [NAME],
  storeName: 'updateAreas',
  tryGenerator: updateAreas,
});

// Profile
function* retrieveProfileData(api) {
  const { profilesList } = yield call(retrieveEntityData, api);
  return profilesList;
}

const profileRetrieveRS = RS({
  nestedPath: [NAME],
  storeName: 'profiles',
  tryGenerator: retrieveProfileData,
});

// Users
function* retrieveUsersData(api) {
  const { users } = yield call(retrieveEntityData, api);
  return users;
}

const userRetrieveRS = RS({
  nestedPath: [NAME],
  storeName: 'users',
  tryGenerator: retrieveUsersData,
});

function* addUser(api, { payload: user }) {
  const id = yield select(userSelectors.getEntityId);
  const response = yield call(api.addEntityUsers, { id, users: [user] });
  return response;
}

const addUserRS = RS({
  nestedPath: [NAME],
  storeName: 'addUser',
  tryGenerator: addUser,
});

function* updateUsers(api, { payload: users }) {
  const id = yield select(userSelectors.getEntityId);
  const response = yield call(api.updateEntityUsers, { id, users: [users] });
  return response;
}

const updateUsersRS = RS({
  nestedPath: [NAME],
  storeName: 'updateUsers',
  tryGenerator: updateUsers,
});

/// ////////////////////////////////////////////////////////////////////////////////

export const sagas = [
  areaRetrieveRS.sagas,
  profileRetrieveRS.sagas,
  userRetrieveRS.sagas,
  entityRetrieveRS.sagas,
  addAreaRS.sagas,
  updateAreasRS.sagas,
  addUserRS.sagas,
  updateUsersRS.sagas,
];

export const reducer = {
  [NAME]: combineReducers({
    ...areaRetrieveRS.reducer,
    ...profileRetrieveRS.reducer,
    ...userRetrieveRS.reducer,
    ...entityRetrieveRS.reducer,
    ...addAreaRS.reducer,
    ...updateAreasRS.reducer,
    ...addUserRS.reducer,
    ...updateUsersRS.reducer,
  }),
};

export const actions = {
  EntityRetrieve: entityRetrieveRS.actions.Fetch,
  EntityAreaRetrieve: areaRetrieveRS.actions.Fetch,
  EntityProfileRetrieve: profileRetrieveRS.actions.Fetch,
  EntityUserRetrieve: userRetrieveRS.actions.Fetch,

  AddArea: addAreaRS.actions.Fetch,
  UpdateAreas: updateAreasRS.actions.Fetch,

  AddUser: addUserRS.actions.Fetch,
  UpdateUsers: updateUsersRS.actions.Fetch,
};

// Selectors
const getEntityData = entityRetrieveRS.selectors.getData;

const getEntityRfcs = createSelector(
  getEntityData,
  (data) => data?.rfcsList,
);

export const selectors = {
  getEntityAreaData: areaRetrieveRS.selectors.getData,
  getEntityAreaRetrieveIsBusy: areaRetrieveRS.selectors.getIsBusy,
  getAddAreaIsBusy: addAreaRS.selectors.getIsBusy,
  getAddAreaIsSuccess: addAreaRS.selectors.getIsSuccess,

  getUpdateAreasIsBusy: updateAreasRS.selectors.getIsBusy,
  getUpdateAreasIsSuccess: updateAreasRS.selectors.getIsSuccess,

  getEntityProfileData: profileRetrieveRS.selectors.getData,
  getEntityProfileRetrieveIsBusy: profileRetrieveRS.selectors.getIsBusy,

  getEntityUserData: userRetrieveRS.selectors.getData,
  getEntityUserRetrieveIsBusy: userRetrieveRS.selectors.getIsBusy,

  getAddUserIsBusy: addUserRS.selectors.getIsBusy,
  getAddUserIsSuccess: addUserRS.selectors.getIsSuccess,

  getUpdateUsersIsBusy: updateUsersRS.selectors.getIsBusy,
  getUpdateUsersIsSuccess: updateUsersRS.selectors.getIsSuccess,

  getEntityRfcs,
};
