import React from 'react';
import PropTypes from 'prop-types';
import Box from 'primitive-components/box';
import { useModal } from 'services/modal';
import { useI18n } from 'services/i18n';
import Text from 'primitive-components/text';
import Button from 'components/button';
import DataBox from 'components/data-box';

const Confirm = ({
  title, message, onConfirm,
}) => {
  const { ModalFooter } = useModal();
  const t = useI18n();

  return (
    <DataBox title={title} minWidth="300px">
      <Box padding="10px" centeredContent maxWidth="500px" minWidth="300px" minHeight="60px">
        <Text>{t(message)}</Text>
      </Box>
      <ModalFooter closeButtonLabel="cancel">
        <Button
          design="primary"
          rounded
          m="10px"
          p="12px 30px"
          onClick={onConfirm}
        >
          {t('accept')}
        </Button>
      </ModalFooter>
    </DataBox>
  );
};

Confirm.propTypes = {
  title: PropTypes.string.isRequired,
  message: PropTypes.string.isRequired,
  onConfirm: PropTypes.func,
};

export default Confirm;
