import { combineReducers } from 'redux';
import { createSelector } from 'reselect';
import { call, put } from 'redux-saga/effects';
import RS, { Reset as ResetRS } from 'helpers/RS';
import { actions as userActions, selectors as userSelectors } from 'store/user';

const NAME = 'auth';

function* SignIn(api, { payload: { email, password } }) {
  const data = yield call(api.signIn, { email, password });

  yield put(userActions.FetchUserRetrieve(email));
  yield put(userActions.FetchUserRetrieveRFCs(email));

  return data;
}

const signInRS = RS({
  nestedPath: [NAME],
  storeName: 'signInRS',
  tryGenerator: SignIn,
  saveToLS: true,
});

/// ////////////////////////////////////////////////////////////////////////////////

function* SignOut(api) {
  yield call(api.signOut);
  yield put(ResetRS());
  return null;
}

const signOutRS = RS({
  nestedPath: [NAME],
  storeName: 'signOutRS',
  tryGenerator: SignOut,
});

/// ////////////////////////////////////////////////////////////////////////////////

export const sagas = [
  // startingSagas,
  signInRS.sagas,
  signOutRS.sagas,
];

export const reducer = {
  [NAME]: combineReducers({
    ...signInRS.reducer,
    ...signOutRS.reducer,
  }),
};

export const actions = {
  SignIn: signInRS.actions.Fetch,
  SignOut: signOutRS.actions.Fetch,
};

// Selectors
const getAuthData = signInRS.selectors.getData;

const getIsAuthenticated = createSelector(
  getAuthData,
  userSelectors.getUserData,
  userSelectors.getUserRFCs,
  (authData, userData, userRfcs) => !!(authData && userData && userRfcs),
);

const getIsBusyAuthenticated = createSelector(
  signInRS.selectors.getIsBusy,
  userSelectors.getIsBusyUserRetrieve,
  userSelectors.getIsBusyUserRetrieveRFCs,
  (isBusySignIn, isBusyUserData, isBusyUserRfcs) => isBusySignIn || isBusyUserData || isBusyUserRfcs,
);

export const selectors = {
  getIsAuthenticated,
  getIsBusyAuthenticated,

  
  getIsBusySignIn: signInRS.selectors.getIsBusy,
};
