import { combineReducers } from 'redux';
import { createSelector } from 'reselect';
import { call } from 'redux-saga/effects';
import RS from 'helpers/RS';
import { userTypes, ableParamsToSearchDocs } from 'store/contants';

const NAME = 'user';

function* UserRetrieveRFCs(api, { payload: email }) {
  const { value } = yield call(api.getUserRetrieveRFC, email);
  return value;
}

const UserRetrieveRFCsRS = RS({
  nestedPath: [NAME],
  storeName: 'userRetrieveRFCs',
  tryGenerator: UserRetrieveRFCs,
  saveToLS: true,
});

/// ////////////////////////////////////////////////////////////////////////////////

function* UserRetrieve(api, { payload: email }) {
  const { value } = yield call(api.getUserRetrieve, email);
  return value;
}

const UserRetrieveRS = RS({
  nestedPath: [NAME],
  storeName: 'userRetrieve',
  tryGenerator: UserRetrieve,
  saveToLS: true,
});

/// ////////////////////////////////////////////////////////////////////////////////

export const sagas = [
  UserRetrieveRS.sagas,
  UserRetrieveRFCsRS.sagas,
];

export const reducer = {
  [NAME]: combineReducers({
    ...UserRetrieveRS.reducer,
    ...UserRetrieveRFCsRS.reducer,
  }),
};

export const actions = {
  FetchUserRetrieve: UserRetrieveRS.actions.Fetch,
  FetchUserRetrieveRFCs: UserRetrieveRFCsRS.actions.Fetch,
};

// Selectors
const getUserData = UserRetrieveRS.selectors.getData;
const getUserRFCs = UserRetrieveRFCsRS.selectors.getData;

const getUserEmail = createSelector(
  getUserData,
  (data) => data?.userEmail,
);

const getUserName = createSelector(
  getUserData,
  (data) => data?.name,
);

const getUserType = createSelector(
  getUserData,
  (data) => data?.userType,
);

const getUserProfile = createSelector(
  getUserData,
  (data) => data?.userProfile,
);

const getRFCsIssuer = createSelector(
  getUserRFCs,
  (rfcs) => rfcs?.senderRfcs,
  // () => ['CCF044459LLT', 'IUR741016FM6'], // Delete After
);

const getRFCsReceiver = createSelector(
  getUserRFCs,
  (rfcs) => rfcs?.receptorRfcs,
  // () => ['QAB886019UJ4'], // Delete After
);

const getMyRFCsFields = createSelector(
  getRFCsIssuer,
  getRFCsReceiver,
  getUserType,
  (rfcIssuer, rfcReceiver, userType) => (userType === userTypes.CLIENT
    ? { key: ableParamsToSearchDocs.RFC_RECEIVER, value: rfcReceiver }
    : { key: ableParamsToSearchDocs.RFC_ISSUER, value: rfcIssuer }),
);

const getEntityId = createSelector(
  getUserData,
  (data) => data?.entityId,
);

export const selectors = {
  getIsBusyUserRetrieve: UserRetrieveRS.selectors.getIsBusy,
  getIsBusyUserRetrieveRFCs: UserRetrieveRFCsRS.selectors.getIsBusy,

  getUserData,
  getUserRFCs,
  getUserName,
  getUserEmail,
  getUserType,
  getUserProfile,
  getRFCsIssuer,
  getRFCsReceiver,
  getMyRFCsFields,

  getEntityId,
};
