import React, { useState } from 'react';
import { useI18n } from 'services/i18n';
import { useDispatch } from 'react-redux';
import styled from 'styled-components';

import Box from 'primitive-components/box';
import PrimitiveButton from 'primitive-components/button';
import PrimitiveForm from 'primitive-components/form';
import Button from 'components/button';

import { actions as authActions } from 'store/auth';

import Checkbox from './Checkbox';
import Input from './Input';

const ForgotPasswordButton = styled(PrimitiveButton)`
  margin-left: auto;
  color: ${({ theme }) => theme.colors.primaryButton};
  text-align: right;
`;

export const defaultUser = {
  email: '',
  password: '',
  keepSession: true,
};

const Form = () => {
  const [user, setUser] = useState(defaultUser);

  const t = useI18n();
  const dispatch = useDispatch();

  const handleLoginSubmit = () => dispatch(authActions.SignIn(user));
  const handleOnChange = (name, value) => setUser({ ...user, [name]: value });

  return (
    <PrimitiveForm width="100%" onSubmit={(e) => e.preventDefault()}>
      <Input
        id="email"
        type="email"
        placeholder={t('email')}
        value={user.email}
        onChange={(e) => handleOnChange('email', e.target.value)}
      />
      <Input
        id="password"
        type="password"
        placeholder={t('password')}
        value={user.password}
        onChange={(e) => handleOnChange('password', e.target.value)}
      />
      <Box display="flex">
        <Checkbox
          id="remember"
          label={t('remember_me')}
          checked={user.keepSession}
          onChange={(value) => handleOnChange('keepSession', value)}
        />
        <ForgotPasswordButton onClick={() => { alert('developing'); }}>
          {t('forgot_password')}
        </ForgotPasswordButton>
      </Box>
      <Box centeredContent>
        <Button
          design="primary"
          rounded
          p="0.75rem 2.5rem"
          mt="2rem"
          id="button_submit"
          type="submit"
          onClick={handleLoginSubmit}
        >
          {t('start_session')}
        </Button>
      </Box>
    </PrimitiveForm>
  );
};

export default Form;
