import React, {
  forwardRef, useState, useEffect,
} from 'react';
import PropTypes from 'prop-types';
import { useI18n } from 'services/i18n';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import { styledSystem } from 'services/theme/helpers';

const Container = styled(Box)`
    display: flex;
    flex-direction: column;
    padding: 0 15px;
    margin-bottom: 14px;
    ${styledSystem};
`;

const StyledContainer = styled(Box)`
	position: relative;
	width: 34px;
	padding: 2px;
	background-color: ${({ theme, enabled }) => (enabled
    ? theme.colors.toggleSwitchEnabledBackground
    : theme.colors.toggleSwitchDisabledBackground)};
	border-radius: 16px;
`;

const StyledCircle = styled(Box)`
	position: relative;
	left: ${({ enabled }) => (enabled ? '50%' : '0')};
	height: 16px;
	width: 16px;
	background-color: #FFFFFF;
	border-radius: 50%;
`;

const Label = styled(Text)`
    margin-bottom: 7px;
`;

const ToggleSwitch = forwardRef((props, ref) => {
  const { enabled, label, onChange } = props;
  const [isEnabled, setIsEnabled] = useState(false);
  const t = useI18n();
  useEffect(() => {
    setIsEnabled(enabled);
  }, [enabled]);

  const handleOnClick = () => {
    setIsEnabled(!isEnabled);
    onChange(!isEnabled);
  };

  return (
    <Container ref={ref} onClick={handleOnClick} {...props}>
      <Label fontSize="sm">{t(label)}</Label>
      <StyledContainer enabled={isEnabled}>
        <StyledCircle enabled={isEnabled} />
      </StyledContainer>
    </Container>
  );
});

ToggleSwitch.defaultProps = {
  enabled: false,
};

ToggleSwitch.propTypes = {
  enabled: PropTypes.bool,
  label: PropTypes.string.isRequired,
  onChange: PropTypes.func,
};

export default ToggleSwitch;
